'use strict';

export default class MainwrapCtrl {
  constructor($scope, $timeout, $state, $mdMedia, $mdSidenav, $http, $sce, Auth, Config) {

    this.$scope = $scope;
    this.$state = $state;
    this.$timeout = $timeout;
    this.$mdMedia = $mdMedia;
    this.$mdSidenav = $mdSidenav;
    this.$http = $http;

    if ( $state.current.name === 'core' ) {
      this.$scope.chapterListExpanded = true;
    }

    this.toggleSidenav = function(menuId) {
      this.$mdSidenav(menuId).toggle();
    };

    this.navIsOpen = function(menuId) {
      return this.$mdSidenav(menuId).isOpen();
    };

    this.Auth = Auth;
    this.Config = Config;
    this.Date = Date;

    this.profileSubnavOpen = false;

    this.$http.get('http://blog.openlegendrpg.com/wp-json/wp/v2/posts?per_page=5&_embed=wp:featuredmedia')
    .then( res => {
      console.log('res',res.data)
      this.blogPosts = res.data;
    });

    $timeout( () => {
      if ( _.has( window, 'twttr' ) ) twttr.widgets.load()
    });

  }

  // non-constructor methods
}
